<template>
  <v-container>
    <v-layout row wrap pa-5>
      <v-flex xs12 md4 v-for="place in placeList" :key="place.id">
        <Place
          :key="place.id"
          :name="place.fcltyNm"
          :address="place.rdnmadr"
          :imgurl="getUrl(place.imgurl)"
        ></Place>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import PlaceService from "../services/placeService";
import Place from "./Place.vue";
export default {
  name: "PlaceList",
  props: ["inputData"],
  data() {
    return {
      placeList: []
    };
  },
  components: {
    Place
  },
  mounted() {
    this.getPlaces();
  },
  methods: {
    getUrl(imgurl) {
      console.log(imgurl);
      if (imgurl == "../assets/image/warning.png") {
        return require("../assets/image/warning.png");
      } else return imgurl;
    },
    async getPlaces() {
      console.log(this.inputData + "가 넘어와써여");

      this.placeList = await PlaceService.getPlacesByFcltyNmLike(
        this.inputData
      );
    }
  }
};
</script>



