<template>
  <v-layout>
    <v-card flat width="100%" height="30vh">
      <!-- <img :src="imgurl" class="place-image"> -->
      <v-img :src="imgurl" class="place-image"></v-img>
      <v-card-title class="card--text">{{name}}</v-card-title>
      <div class="card--text">{{address}}</div>
    </v-card>
  </v-layout>
</template>

<script>
import $ from "jquery";
export default {
  name: "Place",
  props: ["name", "address", "imgurl"],
  method: {}
};
</script>

<style scoped>
.v-sheet {
  border-radius: 0px !important;
}
.v-card:hover {
  cursor: pointer;
  animation-name: hoverEvent;
  animation-duration: 0.5s;
  opacity: 0.5;
}
.v-card {
  position: relative;
}
.card--text {
  position: absolute;
}
.place-image {
  background-size: contain;
}
@keyframes hoverEvent {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0.5;
  }
}
</style>



